
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f5f5;
  line-height: 1.6;
  color: #333;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
}

input {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 8px 12px;
  outline: none;
}

input:focus {
  border-color: #0084ff;
}